.card {
  padding: 20px;
  display: flex;
  flex-direction: column;
  row-gap: 1em;
  background-color: var(--bg-overlay);
  border-radius: .4em;
  transition: all 250ms;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.2);
}

.cardWithActions {
  display: flex;
  flex-direction: column;
  background-color: var(--bg-overlay-light);
  border-radius: .4em;
  transition: all 250ms;
  overflow: hidden;
}

.cardActions {
  padding: .5em;
  padding-left: .8em;
  display: flex;
  gap: .5em;
}

.cardAction {
  cursor: pointer;
  background: none;
  border: none;
  border-radius: .4em;
  opacity: 0.7;
  text-decoration: underline;
  font-size: 1rem;
}

.cardAction:hover {
  opacity: 1;
}

.cardTitle {
  font-size: 1.2rem;
}

.cardSubtitle {
  font: 0.9rem monospace;
  opacity: 0.6;
}